//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START ROBOTO
//light
@font-face {
	@include fontdefault(normal, 300, 'Roboto');
	@include fontpath('Roboto Light', 'Roboto-Light', roboto-light); }
@font-face {
	@include fontdefault(italic, 300, 'Roboto');
	@include fontpath('Roboto Light Italic', 'Roboto-LightItalic', roboto-lightitalic); }
//regular
@font-face {
	@include fontdefault(normal, 400, 'Roboto');
	@include fontpath('Roboto Regular', 'Roboto-Regular', roboto-regular); }
@font-face {
	@include fontdefault(italic, 400, 'Roboto');
	@include fontpath('Roboto Italic', 'Roboto-Italic', roboto-italic); }
//medium
@font-face {
	@include fontdefault(normal, 500, 'Roboto');
	@include fontpath('Roboto Medium', 'Roboto-Medium', roboto-medium); }
@font-face {
	@include fontdefault(italic, 500, 'Roboto');
	@include fontpath('Roboto MediumItalic', 'Roboto-MediumItalic', roboto-mediumitalic); }
//bold
@font-face {
	@include fontdefault(normal, 700, 'Roboto');
	@include fontpath('Roboto Bold', 'Roboto-Bold', roboto-bold); }
@font-face {
	@include fontdefault(italic, 700, 'Roboto');
	@include fontpath('Roboto Bold Italic', 'Roboto-BoldItalic', roboto-bolditalic); }
//END ROBOTO

